import React from 'react';
import * as d3 from 'd3';
import { Bubbles } from '../../fragments';





const VBubbles = () => {
  const [data, setData] = React.useState([]);
  const [grouping, setGrouping] = React.useState('all');
  const [d3Params, setD3Params] = React.useState({})

  

  const onGroupingChanged = (n_grouping) => setGrouping(n_grouping)
  
  React.useEffect(() => {
    const width  = document.getElementById('main').clientWidth // 960
    const height = document.getElementById('main').clientHeight // 640
    const center = { x: width / 2, y: height / 2 }
    const yearCenters = {
      2008: { x: width / 4, y: height / 2 },
      2009: { x: width / 2, y: height / 2 },
      2010: { x: (3 / 4) * width, y: height / 2 },
    }
    setD3Params({ width, height, center, yearCenters })
    
    const createNodes = (rawData) => {
      if (!width || !height)
        return;
  
      const max_amount = d3.max(rawData, d => +d.total_amount);
      const findRadius = d3.scalePow()
        .exponent(0.5)
        .range([2, 85])
        .domain([0, max_amount]);
  
      let nodes = rawData.map(d => ({
        id: d.id,
        radius: findRadius(+d.total_amount),
        value: +d.total_amount,
        name: d.grant_title,
        org: d.organization,
        group: d.group,
        year: d.start_year,
        x: Math.random() * width,
        y: Math.random() * height
      }));
  
      // prevent occlusion of smaller nodes.
      nodes.sort((a, b) => b.value - a.value);
      
      return nodes
    }

    d3.csv('/data/gates_money.csv')
      .then((arr) => {
        setData(createNodes(arr));
      });
  }, []);

  return ( 
    <div style={{ whiteSpace: 'pre-wrap', lineBreak: 'anywhere' }}>
      {(d3Params.width && d3Params.height) && (
        <BubbleChart width={d3Params.width} height={d3Params.height}>
          <Bubbles data={data} forceStrength={0.08} center={d3Params.center} yearCenters={d3Params.yearCenters} groupingYears={grouping === 'year'} />
        </BubbleChart>
      )}
    </div>
  );
}



const BubbleChart = ({width, height, children}) => {
  return ( 
    <svg className="bubbleChart" width={width} height={height}>
      {children}
    </svg>
  );
};



export default VBubbles;