const ModalAlert = (props) => {
  return ( 
    <div style={{ maxHeight: `${80}vh`, overflow: 'auto' }}>
      <div className="text-end pt-2 pe-2">
        <button type='button' className="btn-close" onClick={props.onCancel} />
      </div>
      <div className="py-3" style={{whiteSpace: 'pre-wrap', lineBreak: "anywhere", width: `${480}px`, maxWidth: `${90}vw`}}>
        {props.message}
      </div>
      <div className="text-end">
        <button type='button' className="btn btn-primary text-light fw-bold rounded" onClick={props.onConfirm}>확인</button>
      </div>
    </div>
  );
}

ModalAlert.defaultProps = {
  message: 'NO MESSAGE',
  onConfirm: () => {},
  onCancel : () => {},
}
 
export default ModalAlert;